import React from "react"
import Grid from "@material-ui/core/Grid"
import Container from "@material-ui/core/Container"
import Paper from "@material-ui/core/Paper"
import InputBase from '@material-ui/core/InputBase'
import IconButton from '@material-ui/core/IconButton'
import SearchIcon from '@material-ui/icons/Search'
import Divider from '@material-ui/core/Divider'
import Button from '@material-ui/core/Button'
import makeStyles from "@material-ui/core/styles/makeStyles"
import { Link } from "gatsby"
import Logo from "../static/estrutura/logopresentix.png"
import Facebok from "../static/estrutura/facebook.png"
import Twitter from "../static/estrutura/twitter.png"
import Plus from "../static/estrutura/plus.png"
import Pintres from "../static/estrutura/pintres.png"
import Linkedin from "../static/estrutura/in.png"

const useStyles = makeStyles(theme => ({
  button: {
    color: `#FFFFFF`,
    fontSize: `12px`,
    fontWeight: `300`,
    textDecoration: `none`,
    textTransform: `capitalize`,
    display: `flex`,
    alignItems: `center`,
    "&:hover": {
      color: `#a8b848`
    }
  },
  header: {
    color: `#FFFFFF`,
    fontSize: `12px`,
    display: `flex`,
    flexDirection: "column",
    background: `#446573`,
  },
  minHeader: {
    height: `30px`,
    background: `#3d5a66`,
    display: `flex`,
    alignItems: `center`,
  },
  container: {
    display: `flex`,
    alignItems: `center`,
  },
  logo: {
    height: `30px`,
    width: `243px` ,
    margin:  theme.spacing(2, 0),
  },
  search: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: 400,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 2,
  },
  divider: {
    height: 28,
    margin: 4,
  },
  buttonAcess: {
    margin: theme.spacing(1),
    background: 'linear-gradient(to bottom,  #c3ce5f 0%,#94a837 100%)',
    color: `#FFFFFF`,
    boxShadow: `0 1px 1px 1px rgba(0,0,0,0.2)`,
    "&:hover": {
      background: `linear-gradient(to bottom,  rgba(148,168,55,1) 0%,rgba(195,206,95,1) 100%)`,
      boxShadow: `none`
    },
  },
  buttonLogin: {
    textTransform: `capitalize`,
    color: `#FFFFFF`,
  }
}))

const Header = () => {
  const classes = useStyles()
  return (
    <header className={classes.header}>
      <div className={classes.minHeader}>
        <Container maxWidth="lg" className={classes.container}>
          <Grid container alignItems="center">
            <Grid item xs={12} md={5}>
              <Grid
                container
                alignItems="center"
                spacing={1}
              >
                <Grid item>
                  <Link to="/page-2" className={classes.button}>
                    sobre
                  </Link>
                </Grid>
                <Grid item><span>|</span></Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    suporte
                  </Link>
                </Grid>
                <Grid item><span>|</span></Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    contato
                  </Link>
                </Grid>
                <Grid item><span>|</span></Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    planos
                  </Link>
                </Grid>
              </Grid>
            </Grid>

            <Grid item xs={12} md={7}>
              <Grid
                container
                alignItems="center"
                justify="flex-end"
                spacing={2}
              >
                <Grid item>
                  <Link href="#" className={classes.button}>
                    <div style={{ height: `20px`, width: `20px` }}>
                      <img src={Facebok} alt="Facebook"/>
                    </div>
                  </Link>
                </Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    <div style={{ height: `20px`, width: `20px` }}>
                      <img src={Twitter} alt="Twitter"/>
                    </div>
                  </Link>
                </Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    <div style={{ height: `20px`, width: `20px` }}>
                      <img src={Plus} alt="Plus"/>
                    </div>
                  </Link>
                </Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    <div style={{ height: `20px`, width: `20px` }}>
                      <img src={Pintres} alt="Pintres"/>
                    </div>
                  </Link>
                </Grid>
                <Grid item>
                  <Link href="#" className={classes.button}>
                    <div style={{ height: `20px`, width: `20px` }}>
                      <img src={Linkedin} alt="Linkedin"/>
                    </div>
                  </Link>
                </Grid>

              </Grid>
            </Grid>
          </Grid>
        </Container>
      </div>
      <div>
        <Container maxWidth="lg">
          <Grid container
                direction="row"
                alignItems="center"
                justify="center">
            <Grid item xs={12} md={4}>
              <Link href="#">
                <div className={classes.logo}>
                  <img src={Logo} alt="Apresentações online"/>
                </div>
              </Link>
            </Grid>
            <Grid item xs={12} md={4}>
              <Paper className={classes.search}>
                <InputBase
                  className={classes.input}
                  placeholder="Buscar em todo site"
                />
                <Divider className={classes.divider} orientation="vertical" />
                <IconButton className={classes.iconButton} aria-label="search">
                  <SearchIcon />
                </IconButton>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4} style={ {display: `flex`, justifyContent: `flex-end` } } >
              <Button size={`small`} variant="contained" color="secondary" className={classes.buttonAcess}>
                Cadastre-se Grátis
              </Button>
              <Button size={`small`} className={classes.buttonLogin} >
                Login
              </Button>
            </Grid>
          </Grid>
        </Container>
      </div>
    </header>
  )
}

export default Header
